.buttons {
  display: flex;
  justify-content: center;
}

.button:not(:first-child) {
  @include mq('mobile-only') {
    margin-left: 20px;
  }
  @include mq('tablet') {
    margin-left: 32px;
  }
  margin-left: 20px;
  @include mq('desktop') {
    margin-left: 16px;
  }
}
.button {
  // padding: 12px 20px;
  @include mq('mobile-only') {
    width: 130px;
    height: 44px;
  }
  @include mq('tablet') {
    width: 152px;
    height: 44px;
  }
  @include mq('desktop') {
    width: 148px;
    height: 44px;
  }

  font-family: inherit;
  color: currentColor;
  font-size: 12px;
  letter-spacing: 0.09em;
  text-transform: uppercase;

  background-color: transparent;
  cursor: pointer;
  border-radius: 5px;

  transition: background-color 250ms var(--transition-timing),
    border-color 250ms var(--transition-timing),
    drop-shadow 250ms var(--transition-timing);

  &:hover,
  &:focus {
    filter: drop-shadow(0px 8px 43px rgba(255, 107, 1, 0.6));
    background-color: var(--accent);
    border-color: var(--accent);
  }

  &--dark-mode {
    border: 1px solid var(--white);
    color: var(--white);
  }

  &--light-mode {
    border: 1px solid var(--main-text);
    color: var(--main-text);
  }

  &--active {
    filter: drop-shadow(0px 8px 43px rgba(255, 107, 1, 0.6));
    background-color: var(--accent);
    border-color: var(--accent);
  }
}
